@import '../../styles/mixins/mixins.scss';
@import '../../styles/index.scss';

.card {
  width: 272px;
  background: $bg-color;
  border: $card-border;
  color: $text-color;

  transition: 0.5s;

  @include onTablet {
    width: 229px;
  }

  @include onLowTablet {
    width: 288px;
  }

  @include onPhone {
    width: 287px;
    margin: 0 auto;
  }

  &:hover {
    box-shadow: 0px 2px 16px $card-hover;
  }

  &__content {
    display: flex;
    flex-direction: column;

    padding: 32px;
  }

  &__image {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 196px;
    margin-bottom: 24px;

    transition: 0.5s;
    object-fit: contain;

    @include onPhone {
      max-height: 130px;
    }

    &_img {
      max-width: 100%;
      height: auto;
      max-height: 208px;
      object-fit: contain;

    border-radius: 15px;


      @include onPhone {
        max-height: 130px;
      }
    }

    &:hover {
      transform: scale(1.05);
    }
  }

  &__title {
    font-family: 'Mont-SemiBold';
    font-size: 14px;
    line-height: 21px;
    height: 63px;

    color: $text-color;

    margin-bottom: 8px;
    cursor: pointer;
  }

  &__price {
    display: flex;
    gap: 8px;
    margin-bottom: 8px;

    &_normal {
      font-family: 'Mont-Bold';
      font-size: 22px;
      line-height: 140%;

      color: $text-color;
    }

    &_discount {
      font-family: 'Mont-Regular';
      font-size: 22px;
      line-height: 28px;
      text-decoration-line: line-through;

      color: $btn-discount;
    }
  }

  &__divide {
    border: $card-border-is-selected;
    margin-bottom: 16px;
  }

  &__char {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;

    &_name {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 8px;

      font-family: 'Mont-SemiBold';
      font-size: 12px;
      line-height: 15px;

      color: $secondary-color;
    }

    &_descr {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      gap: 8px;

      font-family: 'Mont-SemiBold';
      font-size: 12px;
      line-height: 15px;
      text-align: right;

      color: $header-btn-is-active;
    }
  }

  &__buy {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &_button {
      display: flex;
      align-items: center;
      justify-content: center;

      padding: 9.5px 39.5px;
      height: 40px;

      font-family: 'Mont-SemiBold';
      font-size: 14px;
      line-height: 21px;
      text-align: center;

      background: $btn-bg-color;
      color: $white;

      border: 1px solid transparent;

      transition: box-shadow 0.5s;
      cursor: pointer;

      @include onTablet {
        padding: 9.5px 18.5px;
      }

      @include onLowTablet {
        padding: 9.5px 47.5px;
      }

      &:hover {
        box-shadow: $card-btn-hover;
      }

      &-isAdded {
        color: $btn-is-added;
        background: $btn-is-added-bg;
        border: $card-border;
        padding: 9.5px 30.5px;
        height: 40px;


        @include onTablet {
          padding: 9.5px 9.5px;
        }
  
        @include onLowTablet {
          padding: 9.5px 39.5px;
        }
      }

    }

    &_favorite {
      display: flex;
      justify-content: center;
      align-items: center;

      width: 40px;
      height: 40px;

      background-color: $btn-is-added-bg;
      border: $border-heart;
      transition: 0.5s;

      cursor: pointer;

      &:hover {
        border: $header-icons-border-hover;
        background: $header-icons-bg-hover;
      }

      &-isSelected {
        border: $card-border-is-selected;
      }
    }
  }
}
